// *** LAYOUT ***
.forecast__discription--rain,
.forecast__discription--wind,
.forecast__discription--sunrise,
.forecast__discription--sunset,
.forecast__discription--humidity,
.forecast__text--01d,
.forecast__text--01n,
.forecast__text--02d,
.forecast__text--02n,
.forecast__text--03d,
.forecast__text--03n,
.forecast__text--04d,
.forecast__text--04n,
.forecast__text--09d,
.forecast__text--09n,
.forecast__text--10d,
.forecast__text--10n,
.forecast__text--11d,
.forecast__text--11n,
.forecast__text--13d,
.forecast__text--13n,
.forecast__text--50d,
.forecast__text--50n {
  display: flex;
  align-items: center;
}

.forecast__list {
  display: flex;
  flex-flow: row;
  justify-content: space-between;
  align-items: center;
}

.forecast__header--hide,
.forecast__term--hide,
.forecast__text--hide {
  display: none;
}

.forecast__card {
  min-width: 8rem;
  padding: auto;
}

